import UserInfo from "./user/userInfo";
import VipInfo from "./user/vipInfo";
export default {
  components: {
    UserInfo,
    VipInfo
  },
  data() {
    return {
      user: {},
      settingConfg: [
        {
          name: "VIP",
          path: "./user/vipRenewal"
        },
        {
          name: "地址"
        },
        {
          name: "浏览",
          path: "./record/index"
        },
        {
          name: "收藏",
          path: "./collection/index"
        }
      ],
      orderConfig: [
        {
          name: "全部订单",
          path: "./order/index"
        },
        {
          name: "待支付",
          path: "./order/index"
        },
        {
          name: "待发货",
          path: "./order/index"
        },
        {
          name: "待收货",
          path: "./order/index",
          icon: "chat-o",
          num: 1
        }
      ],
      serverConfig: [
        {
          name: "合作"
        },
        {
          name: "客服"
        },
        {
          name: "关于"
        }
      ]
    };
  },
  async onReady() {},
  onLoad(options) {
    uni.setNavigationBarTitle({
      title: "浏览记录"
    });
  },
  methods: {
    handlerNavigateToUser(path, settingType) {
      if (settingType === "VIP" && !this.user.vip) {
        this.$router.navigateTo({ url: "./user/vipPay" });
        return;
      }
      this.$router.navigateTo({ url: path });
    },
    handlerNavigateTo(path) {
      this.$router.navigateTo({ url: path });
    },
    handlerNavigateToOrder(path, orderType) {
      this.$router.navigateTo({ url: path });
    }
  }
};
